<template>
  <v-card color="#f1f1f1" flat tile>
    <v-container fluid>
      <v-row>
        <!-- LEFT SIDE: KUIS KETENTUAN LIST -->
        <v-col class="quiz-policy-list">
          <v-container>
            <v-row>
              <!-- TODO change to data-iterator on proper API -->
              <v-data-iterator
                :items="quizList"
                :options.sync="options"
                @pagination="updatePagination"
                hide-default-footer
              >
                <template v-slot:default="props">
                  <v-col cols="12" v-for="item in props.items" :key="item.title">
                    <v-card :key="item.title" raised style="border-radius:15px;">
                      <QuizPolicyListCard
                        :title="item.name"
                        :endTime="item.end_date ? item.end_date : 'Belum ada waktu akhir'"
                        :workingTime="item.duration ? item.duration : 'Belum ada workingTime'"
                        :selectedCard="selectedCard ? selectedCard.name : ''"
                        :id="item.id"
                        @cardSelect="cardSelect"
                      />
                    </v-card>
                  </v-col>
                </template>
                <template v-slot:header>
                  <v-data-footer :pagination.sync="pagination" :options.sync="options">
                    <template v-slot:page-text>{{ options.page }} of {{ numberOfPages }}</template>
                  </v-data-footer>
                </template>
                <template v-slot:footer>
                  <v-data-footer :pagination.sync="pagination" :options.sync="options">
                    <template v-slot:page-text>{{ options.page }} of {{ numberOfPages }}</template>
                  </v-data-footer>
                </template>
              </v-data-iterator>
            </v-row>
          </v-container>
        </v-col>

        <!-- RIGHT SIDE: Kuis Ketentuan Detail Card -->
        <v-col>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-card raised style="border-radius: 15px; padding-left: 0.5em">
                  <QuizDetailCard v-if="selectedCard" :item="selectedCard" type="quizPolicy" />
                </v-card>
              </v-col>
            </v-row>
          </v-container>
        </v-col>
      </v-row>
    </v-container>
  </v-card>
</template>

<script>
import QuizDetailCard from "@/components/QuizDetailCard.vue";
import QuizPolicyListCard from "@/components/QuizPolicyListCard.vue";
import ApiClient from "@/client/index.js";

export default {
  components: {
    QuizDetailCard,
    QuizPolicyListCard
  },
  methods: {
    cardSelect(name) {
      const selfSelect =
        !!this.selectedCard && !!(this.selectedCard.name === name);
      if (selfSelect) {
        this.selectedCard = null;
      } else {
        this.selectedCard = this.quizList.find(item => item.name === name);
      }
    },
    loadQuizz() {
      ApiClient.quizzAll(this.options.page).then(response => {
        this.quizList = response.data;
        this.totalItems = response.total;
        this.pagination.itemsLength = response.total;
      });
    },
    updatePagination(e) {
      this.pagination = e;
    }
  },
  computed: {
    numberOfPages() {
      return Math.ceil(
        this.pagination.itemsLength / this.pagination.itemsPerPage
      );
    }
  },
  data() {
    return {
      selectedCard: null,
      quizList: [],
      pagination: {
        page: 1,
        itemsPerPage: 10,
        pageStart: 1
      },
      options: {
        page: 1,
        itemsPerPage: 10
      },
      totalItems: 0
    };
  },
  watch: {
    options: {
      handler() {
        this.loadQuizz();
      }
    }
  },
  mounted() {
    this.loadQuizz();
  }
};
</script>

<style lang="scss">
.v-data-footer {
  justify-content: center;

  &__select {
    display: none;
  }
  &__icons-before {
    order: -1;
  }
  &__icons-after {
    order: 1;
  }
  &__pagination {
    order: 0;
  }
}
</style>

<style lang="scss" scoped>
.quiz-policy-list {
  max-height: 75vh;
  overflow: auto;
}
</style>
